<template>
  <div class="examinee-exam">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <p class="title-box">
        批次名称：<span class="mr40">{{$route.query.batch}}</span>
        当前报考类别：<span
          v-if="table.list.length">{{table.list[0].firstRecruitType + '/' +table.list[0].secondRecruitType}}</span>
      </p>
      <div class="btn-block">
        <a download href="../gy-files/template/成绩导入模板.xlsx" class="main-c f-small">下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload mr15 ml5">导入录取结果</div>
        </label>
        <input
            accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
            @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
        <div @click="$exportDetail('/export/rStudentInBatchExport.do')" class="btn-main">导出</div>
        <div @click="showDialog()" class="btn-main-deepen ml15">发送通知</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
        <table class="table">
          <thead>
          <tr>
            <th width="5%">
              <el-checkbox v-model="checkAll"></el-checkbox>
            </th>
            <th width="10%">姓名</th>
            <th width="15%">身份证号码</th>
            <th width="13%">联系电话</th>
            <th width="10%">报名来源</th>
            <th width="9%">录取结果</th>
            <th width="13%">面试通知发送状态</th>
            <th width="13%">录取通知发送状态</th>
            <th width="15%">操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,key) in table.list">
            <td>
              <el-checkbox v-model="checkboxList[key]"></el-checkbox>
            </td>
            <td>{{item.name}}</td>
            <td>{{item.idCard || '--'}}</td>
            <td>{{item.phone}}</td>
            <td>{{$enums.ApplyWay.getName(item.applyWay) || '--'}}</td>
            <td>{{$enums.StudentEnrollStatus.getName(item.enrollStatus) || '--'}}</td>
            <td>{{$enums.SendStatus.getName(item.interviewMessageStatus) || '--'}}</td>
            <td>{{$enums.SendStatus.getName(item.enrollMessageStatus) || '--'}}</td>
            <td>
              <span @click="goToDetail(item.id)" class="oper">详情</span>
              <span @click="showEnrollDialog(item)" class="oper">修改录取结果</span>
            </td>
          </tr>
          </tbody>
        </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--修改录取结果-->
      <el-dialog title="修改录取结果" class="dialog" :visible.sync="enrollDialog" width="450px">
        <span class="title">学生姓名：{{enroll.name}}</span>
        <span class="title">当前结果：{{$enums.StudentEnrollStatus.getName(enroll.result) || '--'}}</span>
        <span class="title">是否确认将录取结果修改为"{{enroll.result === 'ENROLL' ? '未录取': '已录取'}}"？</span>
        <div class="btns mt10">
          <div @click="modifyResult" class="btn-submit mr20">确认</div>
          <div @click="enrollDialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
      <!--发送通知-->
      <el-dialog title="发送通知" class="dialog" :visible.sync="dialog" width="450px">
        <span class="mr20">通知类型：</span>
        <el-radio label="1" border>面试通知</el-radio>
        <el-radio label="2" border>录取通知</el-radio>
        <div class="btns mt25">
          <div class="btn-submit mr20">发送</div>
          <div class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data() {
      return {
        nav: [{name: "考试管理", path: "go(-1)"}, {name: "查看考生", path: ""}],
        hide: true,
        searchData: [
          {type: 'select', title: '报名来源', model: 'applyWay', option: this.$enums.ApplyWay.list},
          {type: 'select', title: '录取结果', model: 'isEnroll', option: this.$enums.StudentEnrollStatus.list},
          {type: 'select', title: '面试通知发送状态', model: 'interviewMessageStatus', option: this.$enums.SendStatus.list},
          {type: 'select', title: '录取通知发送状态', model: 'enrollMessageStatus', option: this.$enums.SendStatus.list},
          {type: 'input', placeholder: '姓名/身份证号码'}
        ],
        table: {
          url: '/rstudent/queryStudentPageInBatch.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            batchId: this.$route.query.id,
            applyWay: null,
            isEnroll: null,
            interviewMessageStatus: null,
            enrollMessageStatus: null,
          }
        },
        // 修改录取结果
        enrollDialog: false,
        checkboxList: [false],
        // 录取通知
        dialog: false,
        // 录取结果数据
        enroll: {
          name: '',
          id: null,
          result: ''
        }
      }
    },
    watch: {
      'table.list': function () {
        this.table.list.forEach((e, i) => {
          this.table.list[i].selected = false
          this.checkboxList = new Array(this.table.list.length).fill(false)
        })
      }
    },
    computed: {
      checkAll: {
        get: function () {
          if (this.checkboxList.length > 0) {
            return this.checkboxList.every(item => item)
          } else {
            return false
          }
        },
        set: function (val) {
          this.checkboxList = new Array(this.table.list.length).fill(val)
        }
      }
    },
    methods: {
      // 导入录取结果
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        let formData = new FormData()
        formData.append('file', file)
        formData.append('batchId', this.$route.query.id)
        formData.append('examCentreId', this.$route.query.id)
        let obj = {
          url: '/import/importStudentEnrollList.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          this.$remind('导入成功')
        }
        this.$search(this.table)
        this.$refs.upload.value = null;
      },
      // 修改录取结果
      async modifyResult() {
        let url = '/rstudent/enrollStudent.do'
        if (this.enroll.result === 'ENROLL') {
          url = '/rstudent/disqualifyStudent.do'
        }
        await this.$ajax({
          url,
          query: {
            id: this.enroll.id
          }
        })
        this.$remind('录取结果修改成功')
        this.enrollDialog = false
        this.$search(this.table)
      },
      // 展现修改录取结果弹窗
      showEnrollDialog(item) {
        this.enrollDialog = true
        this.enroll = {
          name: item.name,
          id: item.id,
          result: item.enrollStatus
        }
      },
      // 查看详情
      goToDetail(id) {
        this.$router.push({
          path: `/recruit/examinee/${id}`,
          query: {
            type: this.$route.query.type
          }
        })
      },
      // 获取文化生报考类别
      async getCultureType() {
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'RECRUIT_CULTURE'
          }
        })
        this.searchData[1].option = []
        res.forEach(async (e, i) => {
          this.searchData[1].option.push({
            label: e.name,
            value: e.id,
            children: []
          })

          let res = await this.$ajax({
            url: '/dict/queryDictList.do',
            query: {
              parentId: e.id,
              dictType: e.dictType
            }
          })
          res.forEach(n => {
            this.searchData[1].option[i].children.push({
              label: n.name,
              value: n.id
            })
          })
        })

      },
      // 获取特长生报考类别
      async getSpecialtyType() {
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'RECRUIT_SPECIALITY'
          }
        })
        this.searchData[1].option = []
        res.forEach(e => {
          this.searchData[1].option.push({
            label: e.name,
            value: e.id
          })
        })
      },
      // 获取特长类别
      async getProfessionalList() {
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'SPECIALITY'
          }
        })
        this.searchData[2].option = []
        res.forEach(async (e, i) => {
          this.searchData[2].option.push({
            label: e.name,
            value: e.id,
            children: []
          })

          let res = await this.$ajax({
            url: '/dict/queryDictList.do',
            query: {
              parentId: e.id,
              dictType: e.dictType
            }
          })
          res.forEach(n => {
            this.searchData[2].option[i].children.push({
              label: n.name,
              value: n.id
            })
          })
        })
      },
      handleClick() {
        this.table.query.firstRecruitTypeId = null
        this.table.query.secondRecruitTypeId = null
        if (this.table.query.recruitType === 'SPECIALITY') {
          this.getSpecialtyType()
          this.searchData[2].hide = false
        } else {
          this.searchData[2].hide = true
          this.getCultureType()
        }
        this.$search(this.table)
      },
      //
      showDialog() {
        if (!this.checkboxList.some(item => item)) {
          this.$message.warning('请先选择考生')
          return
        }
        this.dialog = true
      }
    },
    async mounted() {
      await this.$search(this.table)
      this.checkboxList = new Array(this.table.list.length).fill(false)
    }
  }
</script>
<style lang="sass" scoped>
  .examinee-exam
    .title-box
      position: absolute
      line-height: 30px
      top: 17px
    /deep/ .el-radio.is-bordered
      width: 140px
    .btn-block
      .main-c
        vertical-align: top
        display: inline-block
        line-height: 30px
        margin-top: 2px
    .dialog
      .title
        display: block
        margin-bottom: 20px
      .btns
        text-align: center
</style>
